import React from 'react';
import '../styles/game.css';


export default class Adventure extends React.Component{
    constructor(props){
        super(props);
        this.move = this.move.bind(this);
        this.resetEnemy = this.resetEnemy.bind(this);
        this.showSceneLog = this.showSceneLog.bind(this);
        this.state = {
            site : [
                Math.floor(Math.random()*4+1),
                Math.floor(Math.random()*4+1),
                Math.floor(Math.random()*4+1),
                Math.floor(Math.random()*4+1),
                Math.floor(Math.random()*4+1),
                Math.floor(Math.random()*4+1),
                Math.floor(Math.random()*4+1),
                Math.floor(Math.random()*4+1),
                Math.floor(Math.random()*4+1),
                Math.floor(Math.random()*4+1),
                Math.floor(Math.random()*4+1),
                Math.floor(Math.random()*4+1),
                Math.floor(Math.random()*4+1),
                Math.floor(Math.random()*4+1),
                Math.floor(Math.random()*4+1),
                Math.floor(Math.random()*4+1),
                Math.floor(Math.random()*4+1),
                Math.floor(Math.random()*4+1),
                Math.floor(Math.random()*4+1),
                Math.floor(Math.random()*4+1)
            ],
            flag: 0,
            sceneStr: "你来到了一个陌生的地方，你必须找到出路\n",
            sceneStrCount: 1
        }
    }

    showSceneLog(str){
        if(this.state.sceneStrCount/20 >= 1){
            this.setState({
                sceneStr: str,
                sceneStrCount: 1
            });
        }else{
            this.setState({
                sceneStr: str + this.state.sceneStr,
                sceneStrCount: this.state.sceneStrCount + 1
            });
        }
        console.log(str)
    }

    move(direction){
        if(global.gameSetting.scene === 5){
            this.showSceneLog("你最终活着离开了这个地方\n");
        }else if(this.state.flag === 0 || global.gameSetting.moveFlag){
            if(this.state.flag === 0 || direction === this.state.site[this.state.flag]){
                global.gameSetting.riskCount += (30 - global.gameSetting.scene*5);
                global.gameSetting.moveFlag = true;
                this.setState({
                    flag: this.state.flag + 1,
                })
                this.showSceneLog("你找到了正确的道路\n");
            }else if(this.state.flag > 0 && direction === this.state.site[this.state.flag - 1]){
                global.gameSetting.riskCount -= (30 - global.gameSetting.scene*5);
                this.setState({
                    flag: this.state.flag - 1
                })
                this.showSceneLog("你回到了上一个路口\n");
            }else{
                this.emenyLevelUpCheak()
                this.showSceneLog("你遇到了新的敌人\n");
                this.resetEnemy();
            }
            if(global.gameSetting.riskCount/global.gameSetting.riskMax>=1){
                this.setState({
                    flag: 0
                })
                global.gameSetting.scene += 1;
                global.gameSetting.riskCount = 0;
            }
        }else{
            this.showSceneLog("你必须战胜面前的敌人\n");
        }
        
    }
    emenyLevelUpCheak(){
        //敌人升级增强
        if(global.gameEnemy.exp/(global.gameEnemy.level*5 + global.gameEnemy.levelup)>=1){
            global.gameEnemy.level += 1;
            global.gameEnemy.levelup += global.gameEnemy.exp;
            global.gameEnemy.maxHp += Math.floor(Math.random()*global.gameEnemy.baisMaxHp*(10 + global.gameSetting.scene*2) + global.gameEnemy.baisMaxHp*5);
            global.gameEnemy.maxMp += Math.floor(Math.random()*global.gameEnemy.baisMaxMp*(10 + global.gameSetting.scene*2) + global.gameEnemy.baisMaxMp*5);
            global.gameEnemy.acttak += Math.floor(Math.random()*global.gameEnemy.baisActtak*(8 + global.gameSetting.scene*2) + global.gameEnemy.baisActtak*5);
            global.gameEnemy.defend += Math.floor(Math.random()*global.gameEnemy.baisDefend*(8 + global.gameSetting.scene*2) + global.gameEnemy.baisDefend*5);
            global.gameEnemy.speed += Math.floor(Math.random()*global.gameEnemy.baisSpeed*(8 + global.gameSetting.scene*2) + global.gameEnemy.baisSpeed*5);
            global.gameEnemy.regain += Math.floor(Math.random()*global.gameEnemy.baisRegain*(8 + global.gameSetting.scene*2) + global.gameEnemy.baisRegain*5);
            global.gameEnemy.hp = global.gameEnemy.maxHp;
            global.gameEnemy.mp = global.gameEnemy.maxMp;
        }
    }
    resetEnemy(){
        let enemyNames = global.gameEnemyName;
        global.gameEnemy.name = enemyNames[Math.floor(Math.random()*enemyNames.length)];
        let enemyBais = global.gameEnemyBais[global.gameEnemy.name]; 
        
        global.gameEnemy.baisMaxHp = enemyBais[0];
        global.gameEnemy.baisMaxMp = enemyBais[1];
        global.gameEnemy.baisActtak = enemyBais[2];
        global.gameEnemy.baisDefend = enemyBais[3];
        global.gameEnemy.baisSpeed = enemyBais[4];
        global.gameEnemy.baisRegain = enemyBais[5];
        
        //敌人一般增强
        global.gameEnemy.exp += 1;
        global.gameEnemy.maxHp += Math.floor(Math.random()*(global.gameEnemy.baisMaxHp + global.gameSetting.scene));
        global.gameEnemy.maxMp += Math.floor(Math.random()*(global.gameEnemy.baisMaxMp + global.gameSetting.scene));
        global.gameEnemy.acttak += Math.floor(Math.random()*(global.gameEnemy.baisActtak + global.gameSetting.scene));
        global.gameEnemy.defend += Math.floor(Math.random()*(global.gameEnemy.baisDefend + global.gameSetting.scene));
        global.gameEnemy.speed += Math.floor(Math.random()*(global.gameEnemy.baisSpeed + global.gameSetting.scene));
        global.gameEnemy.regain += Math.floor(Math.random()*(global.gameEnemy.baisRegain + global.gameSetting.scene));
        global.gameEnemy.hp = global.gameEnemy.maxHp;
        global.gameEnemy.mp = global.gameEnemy.maxMp;
       
        global.gameSetting.moveFlag = false;
    }
    render(){
        let pAdventureBarBar={
            width: global.gameSetting.riskCount/global.gameSetting.riskMax*300 + "px",
            height: "10px",
            border: "1px solid rgb(0, 0, 0)",
            background: "rgb(0, 0, 0)"
        };
        return(
            <div>
                <fieldset className="adventureBox">
                    <legend>冒险</legend>
                        场景{global.gameSetting.scene}
                        <div className="adventureBar">
                            <div style={pAdventureBarBar}/>
                        </div>
                        <br/>
                        <button className="adventureButton" onClick={this.move.bind(this, 1)}>东</button>
                        <button className="adventureButton" onClick={this.move.bind(this, 2)}>南</button>
                        <button className="adventureButton" onClick={this.move.bind(this, 3)}>西</button>
                        <button className="adventureButton" onClick={this.move.bind(this, 4)}>北</button>
                        <p style={{whiteSpace: 'pre-wrap'}}>{this.state.sceneStr}</p>
                </fieldset>
            </div>
        );
    }
}