import React from 'react';


export default class Weapon extends React.Component{
    constructor(props){
        super(props);
        this.addState = this.addState.bind(this);
        this.state = {
            money: global.gameSetting.money
        }
    }
    addState(station){
        if (global.gameSetting.money >= 100){
            console.log(station);
            if( station === "maxHp"){
                global.gamePlayer.maxHp += 10;
                console.log("你加强了" + global.gamePlayer.maxHp);
            }else if( station === "maxMp"){
                global.gamePlayer.maxMp += 10;
                console.log("你加强了" + global.gamePlayer.maxMp);
            }else if( station === "atk"){
                global.gamePlayer.acttak += 10;
                console.log("你加强了" + global.gamePlayer.acttak);
            }else if( station === "def"){
                global.gamePlayer.defend += 10;
                console.log("你加强了" + global.gamePlayer.defend);
            }else if( station === "spd"){
                global.gamePlayer.speed += 10;
                console.log("你加强了" + global.gamePlayer.speed);
            }else if( station === "rgn"){
                global.gamePlayer.regain += 10;
                console.log("你加强了" + global.gamePlayer.regain);
            }else{
                console.log("加强失败");
            }
            global.gameSetting.money -= 100;
           
        }else{
            console.log("钱不够");
        }
    }
    render(){
        return(
            <div>
                <fieldset className="CharacterBox">
                    <legend>商店</legend>
                    &nbsp; 看看有什么需要的？<br/>
                    &nbsp; （您现在持有{global.gameSetting.money}金钱）<br/>
                    &nbsp; <button className="GameButton" onClick={this.addState.bind(this,"maxHp")}>提高生命值上限</button><br/>
                    &nbsp; <button className="GameButton" onClick={this.addState.bind(this,"maxMp")}>提高魔法值上限</button><br/>
                    &nbsp; <button className="GameButton" onClick={this.addState.bind(this,"atk")}>提高攻击力</button><br/>
                    &nbsp; <button className="GameButton" onClick={this.addState.bind(this,"def")}>提高防御力</button><br/>
                    &nbsp; <button className="GameButton" onClick={this.addState.bind(this,"spd")}>提高速度</button><br/>
                    &nbsp; <button className="GameButton" onClick={this.addState.bind(this,"rgn")}>提高恢复力</button><br/>
                </fieldset>
            </div>
        );
    }
}