import React from 'react';
import '../styles/game.css';
import '../config'
import Fight from './fight';

export default class GameTemplate extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            title:"这是一个放置游戏！"
        }
    }
    componentDidMount(){
        this.timerID = setInterval(
            () => this.game_update(),
            1000
          );
          this.loadData();
        
    }
    componentWillUnmount(){
        this.saveData();
    }
    game_update() {
        if(global.gameSetting.count<global.gameSetting.maxCount){
            global.gameSetting.count+=1;
        }
      }

    handleCTE(){
        if(global.gameSetting.count >= 10){
            global.gameSetting.count -= 10;
            global.gameSetting.energy += 1;
        }
    }
    saveData(){
        let tempGameState = {
            count: global.gameSetting.count,
            maxCount: global.gameSetting.maxCount,
            energy: global.gameSetting.energy,
            money: global.gameSetting.money,
            scene: global.gameSetting.scene
        }
        const gameData = JSON.stringify(tempGameState);
        localStorage.setItem('gameData', gameData);
        console.log("数据保存了");
    }

    loadData(){
        let tempGameState = {};
        if (localStorage.getItem('gameData')) {
            try {
                tempGameState = JSON.parse(localStorage.getItem('gameData'));
                global.gameSetting.count = tempGameState.count;
                global.gameSetting.maxCount = tempGameState.maxCount;
                global.gameSetting.energy = tempGameState.energy;
                global.gameSetting.money = tempGameState.money;
                global.gameSetting.scene = tempGameState.scene;
                console.log("数据加载了");
            } catch(e) {
              localStorage.removeItem('gameData');
              console.log("加载失败了");
            }
        }else{
            console.log("加载失败了2");
        }
    }             
    
    render(){
        return(
            <div>
                <h1 className="GameTitle">{this.state.title}</h1>
                <Fight/>
            </div>
        );
    }
}