import React from 'react';
import '../config'
import '../styles/game.css';
import Character from './character';
import Weapon from './weapon';
import Adventure from './adventure';


export default class Fight extends React.Component{
    constructor(props){
        super(props);
        this.handleFight = this.handleFight.bind(this);
        this.resetPlayer = this.resetPlayer.bind(this);
        this.Allrest = this.Allrest.bind(this);
        this.Regain = this.Regain.bind(this);
        this.loadFight = this.loadFight.bind(this);
        this.saveFight = this.saveFight.bind(this);
        this.AutoFight = this.AutoFight.bind(this);
        this.tick = this.tick.bind(this);
        this.showFightLog = this.showFightLog.bind(this);
        this.nameChange = this.nameChange.bind(this)
        this.state = {
            fightStr:"战斗就绪",
            isAutoFight: false,
            fightLog: "战斗就绪",
            fightLogCount: 0
        }
    }
    showFightLog(str){
        if(this.state.fightLogCount/100 >= 1){
            this.setState({
                fightLog: str,
                fightLogCount: 0
            });
        }else{
            this.setState({
                fightLog: str + this.state.fightLog,
                fightLogCount: this.state.fightLogCount + 1
            });
        }
        console.log(str)
    }

    AutoFight(){
        let str = this.state.isAutoFight?"开启自动战斗\n":"关闭自动战斗\n";
        this.setState({
            isAutoFight: !this.state.isAutoFight,
        });
        this.showFightLog(str)
    }
    componentDidMount(){
        this.timerID = setInterval(
            () => this.tick(),
            1000
          );
        this.loadFight();
    }
    tick(){
        this.Regain();
        if(this.state.isAutoFight){
            this.handleFight();
            this.handleFight();
        }
    }
    handleFight(){
        let tempPlayerState = {
            hp: global.gamePlayer.hp,
            maxHp: global.gamePlayer.maxHp,
            mp: global.gamePlayer.mp,
            maxMp: global.gamePlayer.maxMp,
            exp: global.gamePlayer.exp,
            acttak: global.gamePlayer.acttak,
            defend: global.gamePlayer.defend,
            speed: global.gamePlayer.speed,
            regain: global.gamePlayer.regain,
        }
        let tempEnemyState = {
            hp: global.gameEnemy.hp,
            maxHp: global.gameEnemy.maxHp,
            mp: global.gameEnemy.mp,
            maxMp: global.gameEnemy.maxMp,
            exp: global.gameEnemy.exp,
            acttak: global.gameEnemy.acttak,
            defend: global.gameEnemy.defend,
            speed: global.gameEnemy.speed,
            regain: global.gameEnemy.regain,
        }
        let pDamge = Math.floor(Math.random()*Math.ceil(tempPlayerState.acttak*1.5)+Math.ceil(tempPlayerState.acttak/2))-tempPlayerState.defend;
        let eDamge = Math.floor(Math.random()*Math.ceil(tempEnemyState.acttak*1.5)+Math.ceil(tempEnemyState.acttak/2))-tempEnemyState.defend;
        let pSpeed = Math.floor(Math.random()*tempPlayerState.speed);
        let eSpeed = Math.floor(Math.random()*tempEnemyState.speed);
        let pmA = Math.floor(Math.random()*5+1);
        let emA = Math.floor(Math.random()*5+1);
        pDamge = pDamge > 0 ? pDamge : 0;
        eDamge = eDamge > 0 ? eDamge : 0;
        if(pmA > emA + 2 && global.gamePlayer.mp >= Math.ceil(global.gamePlayer.mp/10)){
            let str1;
            let damage;
            let mcost = Math.ceil(global.gamePlayer.mp/10);
            pmA = Math.floor(Math.random()*7+1);
            if (pmA === 1){
                str1 = "玩家发动雷霆一击";
                damage = (tempPlayerState.acttak)*3;
                global.gameEnemy.hp -= damage;
                mcost *= 2;
            }else if(pmA === 2){
                str1 = "玩家发动连环双刃";
                damage = (tempPlayerState.acttak + tempPlayerState.speed)*2;
                global.gameEnemy.hp -= damage;
                mcost *= 1.5;
            }else if(pmA === 3){
                str1 = "玩家发动舍命一击";
                damage = (tempPlayerState.hp/2);
                global.gameEnemy.hp -= damage;
                global.gamePlayer.hp -= (tempPlayerState.hp/2);
                mcost *= 3;
            }else if(pmA === 4){
                str1 = "玩家发动盾击";
                damage = (tempPlayerState.acttak + tempPlayerState.defend);
                global.gameEnemy.hp -= damage;
                mcost *= 0.5;
            }else if(pmA === 5){
                str1 = "玩家发动奇袭";
                damage = (tempPlayerState.acttak + tempPlayerState.speed*2);
                global.gameEnemy.hp -= damage;
                mcost *= 1.5;
            }else if(pmA === 6){
                str1 = "玩家发动疯狂攻击";
                damage = Math.floor(Math.random()*tempPlayerState.acttak*2);
                global.gameEnemy.hp -= damage;
                global.gamePlayer.hp -= damage;
                mcost *= 1.5;
            }else if(pmA === 7){
                if(global.gamePlayer.hp < global.gamePlayer.maxHp/10){
                    str1 = "玩家发动绝境反击";
                    damage = tempPlayerState.acttak*2 + global.gameEnemy.hp/2;
                    global.gameEnemy.hp -= damage;
                    mcost *= 8;
                }else{
                    str1 = "玩家休整中";
                    damage = 0;
                    global.gamePlayer.hp += tempPlayerState.regain;
                }
                
            }
            this.showFightLog(str1+",造成了"+damage+"点伤害\n");
            global.gamePlayer.mp -= Math.ceil(mcost);
        }else if(emA > pmA + 2 && global.gamePlayer.mp >= Math.ceil(global.gamePlayer.mp/10)){
            let str2;
            let damage;
            let mcost = Math.ceil(global.gamePlayer.mp/10);
            emA = Math.floor(Math.random()*6+1);
            if (emA === 1){
                damage = (tempEnemyState.acttak + tempEnemyState.speed);
                global.gamePlayer.hp -= damage;
                str2 = "电脑发动闪击，造成了"+damage+"点伤害\n";
            }else if(emA === 2){
                damage = tempEnemyState.acttak*5;
                global.gamePlayer.hp -= damage;
                global.gameEnemy.hp -= tempEnemyState.acttak*2;
                str2 = "电脑发动毁灭打击, 造成了"+damage+"点伤害\n";
                mcost *= 5;
            }else if(emA === 3){
                global.gameEnemy.hp += tempEnemyState.regain*10;
                str2 = "电脑发动恢复魔法,恢复了"+tempEnemyState.regain+"点生命值\n";
                mcost *= 2;
            }else if(emA === 4){
                global.gameEnemy.mp += Math.ceil(global.gameEnemy.mp/5);
                str2 = "电脑发动魔法回复术，恢复了"+Math.ceil(global.gameEnemy.mp/5)+"点魔法值\n";
            }else if(emA === 5){
                damage = Math.ceil(global.gamePlayer.hp*0.7);
                global.gamePlayer.hp -= damage;
                str2 = "电脑发动死亡法术,造成了"+damage+"点伤害\n";
                mcost *= 2;
            }else if(emA === 6){
                global.gameEnemy.hp += Math.ceil(global.gamePlayer.maxHp/10*3);
                global.gamePlayer.hp -= global.gamePlayer.maxHp/10;
                str2 = "电脑发动吸血术，吸取了"+Math.ceil(global.gamePlayer.maxHp/10*3)+"点生命值\n";
                mcost *= 2.5;
            }
            global.gameEnemy.mp -= Math.ceil(mcost);
            this.showFightLog(str2);
        }else{
            //先攻
            if(pSpeed >= eSpeed){
                global.gameEnemy.hp -= pDamge;
                this.showFightLog("玩家造成了"+pDamge+"伤害\n");
            }else{
                global.gamePlayer.hp -= eDamge;
                this.showFightLog("电脑造成了"+eDamge+"伤害\n");
            }
            //反击
            if(pSpeed >= eSpeed){
                global.gamePlayer.hp -= eDamge;
                this.showFightLog("电脑反击造成了"+eDamge+"伤害\n");
            }else{
                global.gameEnemy.hp -= pDamge;
                this.showFightLog("玩家反击造成了"+pDamge+"伤害\n");
            }
            //连击
            if(pSpeed >= eSpeed * 2){
                pDamge = Math.floor(Math.random()*tempPlayerState.acttak)
                global.gameEnemy.hp -= pDamge;
                this.showFightLog("玩家造成了"+pDamge+"伤害\n");
            }else if(pSpeed*2 <= eSpeed){
                eDamge = Math.floor(Math.random()*tempEnemyState.acttak)
                global.gamePlayer.hp -= eDamge;
                this.showFightLog("电脑造成了"+eDamge+"伤害\n");
            }
        }
        

        if(global.gamePlayer.hp <0){
            global.gameEnemy.exp += 1;
            this.showFightLog("玩家战败\n")
            this.resetPlayer();
        }else if(global.gameEnemy.hp<0){
            global.gamePlayer.exp += 1;
            global.gameSetting.money += 100;
            this.showFightLog("玩家胜利\n");
            this.playerLevelUpCheck();
            global.gameSetting.moveFlag = true;
            this.setState({
                isAutoFight: false
            })
        }
    }

    playerLevelUpCheck(){
        //玩家升级增强
        if(global.gamePlayer.exp/(global.gamePlayer.level*5 + global.gamePlayer.levelup)>=1){
            global.gamePlayer.level += 1;
            global.gamePlayer.levelup += global.gamePlayer.exp;
            global.gamePlayer.maxHp += Math.floor(Math.random()*global.gamePlayer.baisMaxHp*10 + global.gamePlayer.baisMaxHp*5);
            global.gamePlayer.maxMp += Math.floor(Math.random()*global.gamePlayer.baisMaxMp*10 + global.gamePlayer.baisMaxMp*5);
            global.gamePlayer.acttak += Math.floor(Math.random()*global.gamePlayer.baisActtak*8 + global.gamePlayer.baisActtak*5);
            global.gamePlayer.defend += Math.floor(Math.random()*global.gamePlayer.baisDefend*8 + global.gamePlayer.baisDefend*5);
            global.gamePlayer.speed += Math.floor(Math.random()*global.gamePlayer.baisSpeed*8 + global.gamePlayer.baisSpeed*5);
            global.gamePlayer.regain += Math.floor(Math.random()*global.gamePlayer.baisRegain*8 + global.gamePlayer.baisRegain*5);
            global.gamePlayer.hp = global.gamePlayer.maxHp;
            global.gamePlayer.mp = global.gamePlayer.maxMp;
            this.showFightLog("玩家升级\n");
        }
    }
    resetPlayer(){
        let str = "战斗重置\n";
        //玩家一般增强
        global.gamePlayer.exp += 1;
        global.gamePlayer.maxHp += Math.floor(Math.random()*global.gamePlayer.baisMaxHp);
        global.gamePlayer.maxMp += Math.floor(Math.random()*global.gamePlayer.baisMaxMp);
        global.gamePlayer.acttak += Math.floor(Math.random()*global.gamePlayer.baisActtak);
        global.gamePlayer.defend += Math.floor(Math.random()*global.gamePlayer.baisDefend);
        global.gamePlayer.speed += Math.floor(Math.random()*global.gamePlayer.baisSpeed);
        global.gamePlayer.regain += Math.floor(Math.random()*global.gamePlayer.baisRegain);
        global.gamePlayer.hp = global.gamePlayer.maxHp;
        global.gamePlayer.mp = global.gamePlayer.maxMp;

        global.gameSetting.riskCount = 0;
        this.resetEnemy();
        this.showFightLog(str);
    }
    resetEnemy(){
        global.gameEnemy.hp = global.gameEnemy.maxHp;
        global.gameEnemy.mp = global.gameEnemy.maxMp;
       
        global.gameSetting.moveFlag = false;
    }
    //重置
    Allrest(){
        let str = "完全重置了\n"
        //玩家重置
        global.gamePlayer.level = 1;
        global.gamePlayer.hp = 800;
        global.gamePlayer.maxHp = 800;
        global.gamePlayer.mp = 200;
        global.gamePlayer.maxMp = 200;
        global.gamePlayer.exp = 0;
        global.gamePlayer.acttak = 55;
        global.gamePlayer.defend = 25;
        global.gamePlayer.speed = 22;
        global.gamePlayer.regain = 10;
        global.gamePlayer.levelup = 0;

        //电脑重置
        global.gameEnemy.level = 1;
        global.gameEnemy.hp = 500;
        global.gameEnemy.maxHp = 500;
        global.gameEnemy.mp = 100;
        global.gameEnemy.maxMp = 100;
        global.gameEnemy.exp = 0;
        global.gameEnemy.acttak = 44;
        global.gameEnemy.defend = 20;
        global.gameEnemy.speed = 16;
        global.gameEnemy.regain = 5;
        global.gameEnemy.levelup = 0;
        
        global.gameSetting.money = 0;
        global.gameSetting.scene = 1;
        global.gameSetting.riskCount = 0;
        this.showFightLog(str);
    }

    Regain(){
        let str = "休息中\n";
        //恢复hp
        if((global.gamePlayer.maxHp - global.gamePlayer.hp) < global.gamePlayer.regain*2){
            global.gamePlayer.hp = global.gamePlayer.maxHp;
        }else if (global.gamePlayer.maxHp > global.gamePlayer.hp){
            global.gamePlayer.hp += global.gamePlayer.regain*2;
        }

        /*if((global.gameEnemy.maxHp - global.gameEnemy.hp) < global.gameEnemy.regain*2){
            global.gameEnemy.hp = global.gameEnemy.maxHp;
        }else if (global.gameEnemy.maxHp > global.gameEnemy.hp){
            global.gameEnemy.hp += global.gameEnemy.regain*2;
        }*/
        //恢复MP
        if((global.gamePlayer.maxMp - global.gamePlayer.mp) < Math.ceil(global.gamePlayer.regain*0.1)){
            global.gamePlayer.mp = global.gamePlayer.maxMp;
        }else if (global.gamePlayer.maxMp > global.gamePlayer.mp){
            global.gamePlayer.mp += Math.ceil(global.gamePlayer.regain*0.1);
        }

        /*if((global.gameEnemy.maxMp - global.gameEnemy.mp) < Math.ceil(global.gameEnemy.regain)){
            global.gameEnemy.mp = global.gameEnemy.maxMp;
        }else if (global.gameEnemy.maxMp > global.gameEnemy.mp){
            global.gameEnemy.mp += Math.ceil(global.gameEnemy.regain);
        }*/
        this.showFightLog(str);
    }   

    componentWillUnmount(){
        this.saveFight();
    }
    //保存战斗
    saveFight(){
        let tempPlayerState = {
            level: global.gamePlayer.level,
            hp: global.gamePlayer.hp,
            maxHp: global.gamePlayer.maxHp,
            mp: global.gamePlayer.mp,
            maxMp: global.gamePlayer.maxMp,
            exp: global.gamePlayer.exp,
            acttak: global.gamePlayer.acttak,
            defend: global.gamePlayer.defend,
            speed: global.gamePlayer.speed,
            regain: global.gamePlayer.regain,
            name: global.gamePlayer.name
        }
        let tempEnemyState = {
            level: global.gameEnemy.level,
            hp: global.gameEnemy.hp,
            maxHp: global.gameEnemy.maxHp,
            mp: global.gameEnemy.mp,
            maxMp: global.gameEnemy.maxMp,
            exp: global.gameEnemy.exp,
            acttak: global.gameEnemy.acttak,
            defend: global.gameEnemy.defend,
            speed: global.gameEnemy.speed,
            regain: global.gameEnemy.regain,
            name: global.gameEnemy.name
        }
        let tempGameState = {
            money: 0,
            scene: 1,
            riskCount: 0,
            riskMax: 100
        }
        const playerData = JSON.stringify(tempPlayerState);
        const enemyData = JSON.stringify(tempEnemyState);
        const gameData = JSON.stringify(tempGameState);
        localStorage.setItem('playerData', playerData);
        localStorage.setItem('enemyData', enemyData);
        localStorage.setItem('gameData', gameData);
        console.log("战斗保存了");
        this.showFightLog("战斗保存了\n");
        
    }

    //加载战斗
    loadFight(){
        let tempPlayerState = {};
        let tempEnemyState = {};
        let tempGameState = {};
        if (localStorage.getItem('playerData') && localStorage.getItem('enemyData')) {
            try {
                tempPlayerState = JSON.parse(localStorage.getItem('playerData'));
                tempEnemyState = JSON.parse(localStorage.getItem('enemyData'));
                tempGameState = JSON.parse(localStorage.getItem('gameData'));
                global.gamePlayer.level = tempPlayerState.level;
                global.gamePlayer.hp = tempPlayerState.hp;
                global.gamePlayer.mp = tempPlayerState.mp;
                global.gamePlayer.exp = tempPlayerState.exp;
                global.gamePlayer.maxHp = tempPlayerState.maxHp;
                global.gamePlayer.maxMp = tempPlayerState.maxMp;
                global.gamePlayer.acttak = tempPlayerState.acttak;
                global.gamePlayer.defend = tempPlayerState.defend;
                global.gamePlayer.speed = tempPlayerState.speed;
                global.gamePlayer.regain = tempPlayerState.regain;
                global.gamePlayer.name = tempPlayerState.name;

                global.gameEnemy.level = tempEnemyState.level;
                global.gameEnemy.hp = tempEnemyState.hp;
                global.gameEnemy.mp = tempEnemyState.mp;
                global.gameEnemy.exp = tempEnemyState.exp;
                global.gameEnemy.maxHp = tempEnemyState.maxHp;
                global.gameEnemy.maxMp = tempEnemyState.maxMp;
                global.gameEnemy.acttak = tempEnemyState.acttak;
                global.gameEnemy.defend = tempEnemyState.defend;
                global.gameEnemy.speed = tempEnemyState.speed;
                global.gameEnemy.regain = tempEnemyState.regain;
                global.gameEnemy.name = tempEnemyState.name;

                global.gameSetting.money = tempGameState.money;
                global.gameSetting.scene = tempGameState.scene;
                global.gameSetting.riskCount = tempGameState.riskCount;
                global.gameSetting.riskMax = tempGameState.riskMax;
                this.showFightLog("战斗加载了\n");
            } catch(e) {
              localStorage.removeItem('playerData');
              localStorage.removeItem('enemyData');
              this.showFightLog("加载失败了\n");
            }
        }else{
            this.showFightLog("加载失败了2\n");
        }
    }
    nameChange(e){
        global.gamePlayer.name = e.target.value;
    }
    render(){
        return(
            <div>
                <br/>
                &nbsp; 名字：<input type="text" onChange={this.nameChange} defaultValue={global.gamePlayer.name}/>
                &nbsp; <button className="GameButton" onClick={this.handleFight}>战斗开始</button>
                &nbsp; <button className="GameButton" onClick={this.AutoFight}>{this.state.isAutoFight?"关闭自动自动":"开启自动战斗"}</button>
                &nbsp; <button className="GameButton" onClick={this.saveFight}>保存战斗</button>
                &nbsp; <button className="GameButton" onClick={this.loadFight}>载入战斗</button>
                &nbsp; <button className="GameButton" onClick={this.Allrest}>完全重置</button>
                &nbsp; 
                <Character/>   
                <Weapon />          
                <fieldset className="fightLog">
                        <legend>战斗记录</legend>
                            <p style={{whiteSpace: 'pre-wrap'}}>{this.state.fightLog}</p>
                </fieldset>
                <Adventure/>
            </div>
        );
    }
}