import React from 'react';
import '../styles/game.css';

export default class Character extends React.Component{
    render(){
        let pHpBar={
            width: global.gamePlayer.hp/global.gamePlayer.maxHp*120 + "px",
            height: "10px",
            border: "1px solid rgb(255, 0, 0)",
            background: "rgb(255, 0, 0)"
        };
        let pMpBar={
            width: global.gamePlayer.mp/global.gamePlayer.maxMp*120 + "px",
            height: "10px",
            border: "1px solid rgb(0, 68, 255)",
            background: "rgb(0, 68, 255)"
        };
        let pExpBar={
            width: global.gamePlayer.exp/(global.gamePlayer.level*5 + global.gamePlayer.levelup)*120 + "px",
            height: "10px",
            border: "1px solid rgb(255, 238, 0)",
            background: "rgb(255, 238, 0)"
        };
        let pAtk={
            color: global.gamePlayer.acttak>=global.gameEnemy.acttak? "rgb(255, 0, 0)":"rgb(0, 0, 0)"
        }
        let pDef={
            color: global.gamePlayer.defend>=global.gameEnemy.defend? "rgb(255, 0, 0)":"rgb(0, 0, 0)"
        }
        let pSpd={
            color: global.gamePlayer.speed>=global.gameEnemy.speed? "rgb(255, 0, 0)":"rgb(0, 0, 0)"
        }
        let pRgn={
            color: global.gamePlayer.regain>=global.gameEnemy.regain? "rgb(255, 0, 0)":"rgb(0, 0, 0)"
        }
        let eHpBar={
            width: global.gameEnemy.hp/global.gameEnemy.maxHp*120 + "px",
            height: "10px",
            border: "1px solid rgb(255, 0, 0)",
            background: "rgb(255, 0, 0)"
        };
        let eMpBar={
            width: global.gameEnemy.mp/global.gameEnemy.maxMp*120 + "px",
            height: "10px",
            border: "1px solid rgb(0, 68, 255)",
            background: "rgb(0, 68, 255)"
        };
        let eExpBar={
            width: global.gameEnemy.exp/(global.gameEnemy.level*5 + global.gameEnemy.levelup)*120 + "px",
            height: "10px",
            border: "1px solid rgb(255, 238, 0)",
            background: "rgb(255, 238, 0)"
        };
        let eAtk={
            color: global.gameEnemy.acttak>global.gamePlayer.acttak? "rgb(255, 0, 0)":"rgb(0, 0, 0)"
        }
        let eDef={
            color: global.gameEnemy.defend>global.gamePlayer.defend? "rgb(255, 0, 0)":"rgb(0, 0, 0)"
        }
        let eSpd={
            color: global.gameEnemy.speed>global.gamePlayer.speed? "rgb(255, 0, 0)":"rgb(0, 0, 0)"
        }
        let eRgn={
            color: global.gameEnemy.regain>global.gamePlayer.regain? "rgb(255, 0, 0)":"rgb(0, 0, 0)"
        }
        return(
            <div>
                <fieldset className="CharacterBox">
                    <legend>{global.gamePlayer.name}</legend>
                    &nbsp; 等级：{global.gamePlayer.level}
                    <br/>
                    &nbsp; 生命值:
                    <div className="maxHpBar">
                        <div style={pHpBar}/>
                    </div>
                    <br/>
                    &nbsp; 魔法值：
                    <div className="maxMpBar">
                        <div style={pMpBar}/>
                    </div>
                    <br/>
                    &nbsp; 经验值：
                    <div className="maxExpBar">
                        <div style={pExpBar}/>
                    </div>
                    <p style={pAtk}>&nbsp; 攻击力：{global.gamePlayer.acttak}</p>
                    <p style={pDef}>&nbsp; 防御力：{global.gamePlayer.defend}</p>
                    <p style={pSpd}>&nbsp; 速度：{global.gamePlayer.speed}</p>
                    <p style={pRgn}>&nbsp; 恢复力：{global.gamePlayer.regain}</p>
                </fieldset>
                <fieldset className="CharacterBox">
                    <legend>{global.gameEnemy.name}</legend>
                    &nbsp; 等级：{global.gameEnemy.level}<br/>
                    &nbsp; 生命值:
                    <div className="maxHpBar">
                        <div style={eHpBar}/>
                    </div>
                    <br/>
                    &nbsp; 魔法值：
                    <div className="maxMpBar">
                        <div style={eMpBar}/>
                    </div>
                    <br/>
                    &nbsp; 经验值：
                    <div className="maxExpBar">
                        <div style={eExpBar}/>
                    </div>
                    <br/>
                    <p style={eAtk}>&nbsp; 攻击力：{global.gameEnemy.acttak}</p>
                    <p style={eDef}>&nbsp; 防御力：{global.gameEnemy.defend}</p>
                    <p style={eSpd}>&nbsp; 速度：{global.gameEnemy.speed}</p>
                    <p style={eRgn}>&nbsp; 恢复力：{global.gameEnemy.regain}</p>
                </fieldset>
                
            </div>
        );
    }
}